import React from 'react';
import './App.css';
import Blog from './components/Blog.js';
import BlogPage from './components/Blogs/BlogPage.js';
import About from './About.js';
import Nav from './components/navigation.js';
import {BrowserRouter as Router, Switch, Route } from 'react-router-dom';
function App() {
  return (
    <Router>
      <div className="App">
          <Nav/>
          <Switch>
            <Route path ="/" exact component = {Home}/>
            <Route path="/Blog" component = {Blog}/>
            <Route path="/About" component = {About}/>
              <Route path = "/Blog/:id" component = {BlogPage} />
          </Switch>
      </div>
    </Router>
  );
}
const Home = () => {
  return(
    <div className = "homepage">
      <div className = "landing-image" style = {{background:"url('https://source.unsplash.com/user/jlo/2400x1600') center no-repeat" , width: "100vw", height: "45vh", backgroundSize: "cover"} }> </div>
      <h1> Home </h1>
      <div className = "Triple">
        <div className ="Tsection"></div>
        <div className ="Tsection"></div>
        <div className ="Tsection"></div>
      </div>
    </div>
  )
}

export default App;
